* {
  box-sizing: border-box;
}

body {
  text-align: center;
  margin: 16px;
  padding-top: 10px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  overflow: auto;
}

li, .main-item {
  margin-bottom: 50px;
}

.custom-btn {
  width: 50%;
  color: #fff;
  cursor: pointer;
  background: none;
  border-radius: 5px;
  outline: none;
  padding: 10px 25px;
  font-family: Lato, sans-serif;
  font-weight: 500;
  transition: all .3s;
  display: inline-block;
  position: relative;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .5), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

.main-link {
  background: none;
  border: none;
  padding: 0;
  line-height: 42px;
  text-decoration: none;
}

.main-link span {
  width: 100%;
  height: 100%;
  color: #000;
  display: block;
  position: relative;
}

.main-link :before, .main-link :after {
  content: "";
  height: 0%;
  width: 1px;
  background: none;
  position: absolute;
  box-shadow: -1px -1px 20px #fff, -4px -4px 5px #fff, 7px 7px 20px rgba(0, 0, 0, .4), 4px 4px 5px rgba(0, 0, 0, .3);
}

.main-link :before {
  transition: all .5s;
  top: 0;
  right: 0;
}

.main-link :after {
  transition: all .5s;
  bottom: 0;
  left: 0;
}

.main-link :hover {
  color: #76aef1;
  box-shadow: none;
  background: none;
}

.main-link :hover:before, .รง :hover:after {
  height: 100%;
  transition: all .5s;
}

.main-link span:before, .main-link span:after {
  content: "";
  background: none;
  position: absolute;
  box-shadow: -1px -1px 20px #fff, -4px -4px 5px #fff, 7px 7px 20px rgba(0, 0, 0, .4), 4px 4px 5px rgba(0, 0, 0, .3);
}

.main-link span:before {
  width: 0%;
  height: .5px;
  transition: all .5s;
  top: 0;
  left: 0;
}

.main-link span:after {
  width: 0%;
  height: .5px;
  transition: all .5s;
  bottom: 0;
  right: 0;
}

.main-link span:hover:before, .main-link span:hover:after {
  width: 100%;
}

button {
  color: #ae00ff;
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  border: 1px solid #303030;
  border-radius: 20px;
  outline: none;
  padding: 15px 40px;
  font-size: 15px;
  font-weight: bold;
  transition: all .2s;
  position: relative;
  overflow: hidden;
}

button:hover {
  transition-delay: .6s;
  box-shadow: 0 0 10px #ae00ff, 0 0 25px #001eff, 0 0 50px #ae00ff;
}

button span {
  position: absolute;
}

button span:nth-child(1) {
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0), #ae00ff);
  top: 0;
  left: -100%;
}

button:hover span:nth-child(1) {
  transition: all .7s;
  left: 100%;
}

button span:nth-child(3) {
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0), #001eff);
  bottom: 0;
  right: -100%;
}

button:hover span:nth-child(3) {
  transition: all .7s .35s;
  right: 100%;
}

button span:nth-child(2) {
  width: 2px;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0), #ae00ff);
  top: -100%;
  right: 0;
}

button:hover span:nth-child(2) {
  transition: all .7s .17s;
  top: 100%;
}

button:active {
  color: #bfbfbf;
  background: linear-gradient(to top right, #ae00af, #001eff);
  transition: all .1s;
  box-shadow: 0 0 8px #ae00ff, 0 0 8px #001eff, 0 0 8px #ae00ff;
}

